import Box from '@mui/material/Box';
import ImageList from '@mui/material/ImageList';
import ImageListItem from '@mui/material/ImageListItem';
import Modal from '@mui/material/Modal';
import {useState} from 'react';

const itemData = [
	{
		img: 'assets/img/1.jpg',
		title: '1'
	},
	{
		img: 'assets/img/2.jpg',
		title: '2'
	},
	{
		img: 'assets/img/3.jpg',
		title: '3'
	},
	{
		img: 'assets/img/4.jpg',
		title: '4'
	},
	{
		img: 'assets/img/5.jpg',
		title: '5'
	},
	{
		img: 'assets/img/6.jpeg',
		title: '6'
	},
	{
		img: 'assets/img/7.jpeg',
		title: '7'
	},
	{
		img: 'assets/img/8.jpeg',
		title: '8'
	},
	{
		img: 'assets/img/9.jpeg',
		title: '9'
	},
	{
		img: 'assets/img/10.jpeg',
		title: '10'
	},
	{
		img: 'assets/img/11.jpg',
		title: '11'
	},
	{
		img: 'assets/img/12.jpg',
		title: '12'
	}
];

export default function GalleryPage() {
	const [modalImage, setModalImage] = useState('');
	const handleClose = () => setModalImage('');
	return (
		<Box sx={{p: 2}}>
			<Box mx="auto" maxWidth="md">
				<ImageList cols={3} gap={8} variant="masonry">
					{itemData.map(item => (
						<ImageListItem key={item.img}>
							<img src={item.img} alt={item.title} loading="lazy" onClick={() => setModalImage(item.img)} />
						</ImageListItem>
					))}
				</ImageList>
			</Box>
			<Modal
				open={Boolean(modalImage)}
				onClose={handleClose}
				sx={{
					display: 'flex',
					justifyContent: 'center',
					alignItems: 'center',
					p: 8
				}}
			>
				<img
					src={modalImage}
					loading="lazy"
					onClick={() => setModalImage('')}
					style={{
						height: 'auto',
						maxWidth: '100%',
						maxHeight: '100%',
						objectFit: 'scale-down'
					}}
				/>
			</Modal>
		</Box>
	);
}
