import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';

import TitleTypography from './TitleTypography';
import DotTypography from './DotTypography';

type Properties = {
	title: string;
	description: string;
	list: string[];
};

export default function DotCard({title, description, list}: Properties) {
	return (
		<Grid display="flex" size={{xs: 12, md: 6}}>
			<Paper sx={{padding: 2, flexGrow: 1}}>
				<TitleTypography title={title} />
				<Typography textAlign={'start'}>{description}</Typography>
				{list.map(textline => (
					<Box key={textline} flexDirection={'row'} textAlign={'start'} padding={1}>
						<DotTypography />
						<Typography display="inline">{textline}</Typography>
					</Box>
				))}
			</Paper>
		</Grid>
	);
}
