import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';

export default function CompanyDetails() {
	return (
		<Box maxWidth="md" mx="auto" textAlign="center" marginTop={2}>
			<Typography variant="h1">Învață prin Joacă</Typography>
			<Box maxWidth="md" mx="auto" marginTop={2} px={2}>
				<Typography variant="h6">Evenimente de dezvoltare cognitivă și socială pentru copii.</Typography>
			</Box>
		</Box>
	);
}
