import {Box, Grid, Paper, Typography} from '@mui/material';
import moment from 'moment';

import {EventData} from '../events-data-base/types';

import EventCard from './EventCard';

type Properties = {
	title: string;
	events: EventData[];
};

export default function EventList({title, events}: Properties) {
	const sortedEvents = events.sort((event1, event2) => {
		const time1 = moment(event1.date);
		const time2 = moment(event2.date);
		if (time1.isSame(time2)) {
			return 0;
		}

		if (time1.isBefore(time2)) {
			return -1;
		}
		return 1;
	});
	return (
		<Box maxWidth="md" mx="auto" marginTop={2}>
			<Typography ml={2} variant="h4">
				{title}
			</Typography>
			<Grid container maxWidth="md" mx="auto" spacing={2} my={2}>
				{sortedEvents.map(event => (
					<Grid
						key={event.id}
						size={{xs: 12, md: 6}}
						display="flex"
						flexDirection="row"
						justifyContent={{
							md: 'flex-start',
							xs: 'center'
						}}
					>
						<Paper sx={{padding: 2}}>
							<EventCard event={event} />
						</Paper>
					</Grid>
				))}
			</Grid>
		</Box>
	);
}
