import moment from 'moment';
import Box from '@mui/material/Box';

import PageDescription from './page-description/PageDescription';
import EventList from './event-list/EventList';
import {events} from './events-data-base/events';
import {EventData} from './events-data-base/types';

export default function EventsPage() {
	const {past, future} = events.reduce(
		(accumulator: {past: EventData[]; future: EventData[]}, event: EventData) => {
			const today = moment();
			if (moment(event.date).isBefore(today)) {
				return {
					...accumulator,
					past: [...accumulator.past, event]
				};
			} else {
				return {
					...accumulator,
					future: [...accumulator.future, event]
				};
			}
		},
		{past: [], future: []}
	);

	return (
		<Box sx={{p: 2}}>
			<PageDescription />
			<EventList title="Evenimente viitoare" events={future} />
			<EventList title="Evenimente precedente" events={past} />
		</Box>
	);
}
