import Paper from '@mui/material/Paper';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Stack from '@mui/material/Stack';
import LocationPinIcon from '@mui/icons-material/LocationPin';
import MailIcon from '@mui/icons-material/Mail';
import PhoneIcon from '@mui/icons-material/Phone';
import IconButton from '@mui/material/IconButton';
import FacebookIcon from '@mui/icons-material/Facebook';
import InstagramIcon from '@mui/icons-material/Instagram';
import {useTheme} from '@mui/material';

const TikTokIcon = ({color = '#000000'}: {color?: string}) => (
	<svg fill={color} xmlns="http://www.w3.org/2000/svg" viewBox="0 0 50 50" width="100%" height="100%">
		<path d="M41,4H9C6.243,4,4,6.243,4,9v32c0,2.757,2.243,5,5,5h32c2.757,0,5-2.243,5-5V9C46,6.243,43.757,4,41,4z M37.006,22.323 c-0.227,0.021-0.457,0.035-0.69,0.035c-2.623,0-4.928-1.349-6.269-3.388c0,5.349,0,11.435,0,11.537c0,4.709-3.818,8.527-8.527,8.527 s-8.527-3.818-8.527-8.527s3.818-8.527,8.527-8.527c0.178,0,0.352,0.016,0.527,0.027v4.202c-0.175-0.021-0.347-0.053-0.527-0.053 c-2.404,0-4.352,1.948-4.352,4.352s1.948,4.352,4.352,4.352s4.527-1.894,4.527-4.298c0-0.095,0.042-19.594,0.042-19.594h4.016 c0.378,3.591,3.277,6.425,6.901,6.685V22.323z" />
	</svg>
);

export default function AppFooter() {
	const theme = useTheme();
	return (
		<Paper square={true} sx={{py: 4}}>
			<Box maxWidth="md" mx="auto" sx={{px: 2}}>
				<Stack direction={{xs: 'column', sm: 'row'}} justifyContent="space-evenly" gap={2}>
					<Stack direction="column" spacing={2}>
						<Typography variant="h6">Învață prin Joacă</Typography>
						<Typography>Jocul potrivit pentru dezvoltarea copilului tău!</Typography>
						<Stack direction="row">
							<IconButton aria-label="Link Facebook">
								<FacebookIcon
									onClick={() => {
										window.open('https://www.facebook.com/invataprinjoaca1', '_blank');
									}}
								/>
							</IconButton>
							<IconButton aria-label="Link Instagram">
								<InstagramIcon
									onClick={() => {
										window.open('https://www.instagram.com/invataprinjoaca1', '_blank');
									}}
								/>
							</IconButton>
							<IconButton
								aria-label="Link TikTok"
								onClick={() => window.open('https://www.tiktok.com/@invataprinjoaca', '_blank')}
							>
								<TikTokIcon color={theme?.vars?.palette.text.primary} />
							</IconButton>
						</Stack>
					</Stack>
					<Stack direction="column" spacing={2}>
						<Stack direction="row" spacing={1} alignItems="center">
							<LocationPinIcon />
							<Typography
								onClick={() => {
									window.open('https://maps.app.goo.gl/XsHe1Eo23aRyQNNh9', '_blank');
								}}
							>
								Șoseaua Bârnova 150, C3
							</Typography>
						</Stack>
						<Stack direction="row" spacing={1} alignItems="center">
							<MailIcon />
							<a href="mailto:invataprinjoaca@gmail.com" style={{color: 'inherit', textDecoration: 'none '}}>
								<Typography>invataprinjoaca@gmail.com</Typography>
							</a>
						</Stack>
						<Stack direction="row" spacing={1} alignItems="center">
							<PhoneIcon />
							<a href="tel:0750282905" style={{color: 'inherit', textDecoration: 'none '}}>
								<Typography>0750 282 905</Typography>
							</a>
						</Stack>
					</Stack>
				</Stack>
			</Box>
		</Paper>
	);
}
