import Paper from '@mui/material/Paper';
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';

import TitleTypography from './TitleTypography';
import DotTypography from './DotTypography';

type Properties = {
	title: string;
	list: {
		first: string;
		second: string;
	}[];
};
export default function BolderCard({title, list}: Properties) {
	return (
		<Grid display="flex" size={{xs: 12, md: 6}}>
			<Paper sx={{padding: 2, flexGrow: 1}}>
				<TitleTypography title={title} />
				{list.map(({first, second}) => (
					<Box key={first} flexDirection={'row'} textAlign={'start'} padding={1}>
						<DotTypography />
						<Typography display="inline" fontWeight={700} sx={{mr: 1}}>
							{first}
						</Typography>
						<Typography display="inline">{second}</Typography>
					</Box>
				))}
			</Paper>
		</Grid>
	);
}
