import {Route, HashRouter as Router, Routes} from 'react-router-dom';
import CssBaseline from '@mui/material/CssBaseline';
import Box from '@mui/material/Box';

import AppTheme from './shared-theme/AppTheme';
import LandingPage from './pages/landing-page/LandingPage';
import GameListPage from './pages/game-list-page/GameListPage';
import GalleryPage from './pages/gallery-page/GalleryPage';
import EventsPage from './pages/events-page/EventPage';
import AppAppBar from './common/AppAppBar';
import AppFooter from './common/AppFooter';
import './app.scss';

function App() {
	return (
		<AppTheme>
			<CssBaseline enableColorScheme />
			<Router>
				<AppAppBar />
				<Box
					id="LandingPage"
					sx={theme => ({
						width: '100%',
						backgroundRepeat: 'no-repeat',

						backgroundImage: 'radial-gradient(ellipse 80% 50% at 50% -20%, hsl(210, 100%, 90%), transparent)',
						...theme.applyStyles('dark', {
							backgroundImage: 'radial-gradient(ellipse 80% 50% at 50% -20%, hsl(210, 100%, 16%), transparent)',
							display: 'flex',
							flexDirection: 'column',
							gap: 3
						})
					})}
					paddingTop={12}
				>
					<Routes>
						<Route index element={<LandingPage />} />
						<Route path="/game-list" element={<GameListPage />} />
						<Route path="/events-page" element={<EventsPage />} />
						<Route path="/gallery-page" element={<GalleryPage />} />
					</Routes>
				</Box>
				<AppFooter />
			</Router>
		</AppTheme>
	);
}

export default App;
