import * as React from 'react';
import {styled} from '@mui/material/styles';
import Card from '@mui/material/Card';
import CardHeader from '@mui/material/CardHeader';
import CardMedia from '@mui/material/CardMedia';
import CardContent from '@mui/material/CardContent';
import CardActions from '@mui/material/CardActions';
import Collapse from '@mui/material/Collapse';
import Avatar from '@mui/material/Avatar';
import IconButton, {IconButtonProps} from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import FacebookIcon from '@mui/icons-material/Facebook';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import moment from 'moment';

import {EventData} from '../events-data-base/types';

interface ExpandMoreProperties extends IconButtonProps {
	expand: boolean;
}

const ExpandMore = styled((properties: ExpandMoreProperties) => {
	// eslint-disable-next-line @typescript-eslint/no-unused-vars
	const {expand, ...other} = properties;
	return <IconButton {...other} />;
})(({theme}) => ({
	marginLeft: 'auto',
	transition: theme.transitions.create('transform', {
		duration: theme.transitions.duration.shortest
	}),
	variants: [
		{
			props: ({expand}) => !expand,
			style: {
				transform: 'rotate(0deg)'
			}
		},
		{
			props: ({expand}) => Boolean(expand),
			style: {
				transform: 'rotate(180deg)'
			}
		}
	]
}));
type Properties = {
	event: EventData;
};
export default function EventCard({event}: Properties) {
	const [expanded, setExpanded] = React.useState(false);

	const handleExpandClick = () => {
		setExpanded(!expanded);
	};

	const formattedDate = moment(event.date).format('dddd D MMMM YYYY H:mm');
	return (
		<Card sx={{maxWidth: 345, whiteSpace: 'pre-line', textAlign: 'left'}}>
			<CardHeader
				slotProps={{
					title: {
						fontWeight: 800
					}
				}}
				avatar={<Avatar alt="logo" src="assets/logo.png" />}
				title={event.name}
				subheader={formattedDate}
			/>
			<CardMedia component="img" height="194" image={event.image} />
			<CardContent>
				<Typography variant="body2" sx={{color: 'text.secondary'}}>
					{event.shortDescription}
				</Typography>
			</CardContent>

			<CardActions disableSpacing>
				<IconButton aria-label="Link Facebook">
					<FacebookIcon
						onClick={() => {
							window.open(event.linkFacebook, '_blank');
						}}
					/>
				</IconButton>
				<ExpandMore expand={expanded} onClick={handleExpandClick} aria-expanded={expanded} aria-label="show more">
					<ExpandMoreIcon />
				</ExpandMore>
			</CardActions>
			<Collapse in={expanded} timeout="auto" unmountOnExit>
				<CardContent>
					<Typography sx={{marginBottom: 2}}>{event.description}</Typography>
				</CardContent>
			</Collapse>
		</Card>
	);
}
