import {ChangeEvent, Fragment, useState} from 'react';
import Paper from '@mui/material/Paper';
import TableContainer from '@mui/material/TableContainer';
import Table from '@mui/material/Table';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import TableCell, {tableCellClasses} from '@mui/material/TableCell';
import TableBody from '@mui/material/TableBody';
import TablePagination from '@mui/material/TablePagination';
import TableSortLabel from '@mui/material/TableSortLabel';
import Collapse from '@mui/material/Collapse';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import IconButton from '@mui/material/IconButton';
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import KeyboardArrowUpIcon from '@mui/icons-material/KeyboardArrowUp';
import {styled} from '@mui/material/styles';

import {colorSchemes} from '../../../../shared-theme/themePrimitives';

import {Column, Row} from './types';
import rows from './data';
import GamesTableCell from './GameTableCell';

const StyledTableCell = styled(TableCell)(({theme}) => ({
	[`&.${tableCellClasses.head}`]: {
		backgroundColor: theme.palette.primary.dark,
		color: theme.palette.common.white
	},
	[`&.${tableCellClasses.body}`]: {
		fontSize: 14
	}
}));

const StyledTableRow = styled(TableRow)(({theme}) => ({
	'&:nth-of-type(odd)': {
		backgroundColor: theme.palette.action.hover
	},
	// hide last border
	'&:last-child td, &:last-child th': {
		border: 0
	}
}));

const columns: readonly Column[] = [
	{
		id: 'image',
		label: '',
		align: 'center'
	},
	{
		id: 'name',
		label: 'Nume'
	},
	{
		id: 'minAge',
		label: 'Vârstă minimă'
	},
	{
		id: 'complexity',
		label: 'Complexitate'
	},
	{
		id: 'linkBGG',
		label: 'Link BoardGameGeek'
	}
];

export default function GamesTable() {
	const [expandedRowID, setExpandedRowID] = useState(-1);

	const [page, setPage] = useState(0);
	const [rowsPerPage, setRowsPerPage] = useState(10);
	const [order, setOrder] = useState('asc');
	const [orderBy, setOrderBy] = useState('');

	const handleChangePage = (event: unknown, newPage: number) => {
		setPage(newPage);
	};

	const handleChangeRowsPerPage = (event: ChangeEvent<HTMLInputElement>) => {
		setRowsPerPage(Number(event.target.value));
		setPage(0);
	};

	const updatedRows = rows
		.sort((firstRow: Row, secondRow: Row) => {
			const firstValue = firstRow[orderBy];
			const secondValue = secondRow[orderBy];

			const direction = order === 'asc' ? 1 : -1;

			if (firstValue > secondValue) {
				return direction;
			}
			if (firstValue < secondValue) {
				return -direction;
			}
			return 0;
		})
		.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage);

	return (
		<Paper sx={{width: '100%', overflow: 'hidden'}}>
			<TableContainer>
				<Table stickyHeader aria-label="sticky table">
					<TableHead>
						<StyledTableRow>
							<StyledTableCell />
							{columns.map(column => {
								console.log;
								const isColumnSorted = orderBy === column.id;
								return (
									<StyledTableCell key={column.id}>
										<TableSortLabel
											active={isColumnSorted}
											key={column.id}
											style={{
												minWidth: column.minWidth,
												color: colorSchemes.dark.palette.text.primary,
												fontWeight: isColumnSorted ? 700 : 500
											}}
											direction={(isColumnSorted ? order : 'asc') as any}
											onClick={() => {
												if (isColumnSorted) {
													setOrder(order === 'asc' ? 'desc' : 'asc');
												}
												setOrderBy(column.id);
											}}
										>
											{column.label}
										</TableSortLabel>
									</StyledTableCell>
								);
							})}
						</StyledTableRow>
					</TableHead>
					<TableBody>
						{updatedRows.map(row => (
							<Fragment key={row.id}>
								<StyledTableRow>
									<TableCell>
										<IconButton
											aria-label="expand row"
											size="small"
											onClick={() => setExpandedRowID(expandedId => (expandedId === row.id ? -1 : row.id))}
										>
											{expandedRowID === row.id ? <KeyboardArrowUpIcon /> : <KeyboardArrowDownIcon />}
										</IconButton>
									</TableCell>
									{columns.map(column => (
										<GamesTableCell key={column.id} column={column} row={row} />
									))}
								</StyledTableRow>
								<StyledTableRow>
									<TableCell style={{paddingBottom: 0, paddingTop: 0}} colSpan={6}>
										<Collapse in={expandedRowID === row.id} timeout="auto" unmountOnExit>
											<Box sx={{margin: 1}}>
												<Typography variant="body1" gutterBottom component="div">
													{row.description}
												</Typography>
											</Box>
										</Collapse>
									</TableCell>
								</StyledTableRow>
							</Fragment>
						))}
					</TableBody>
				</Table>
			</TableContainer>
			<TablePagination
				rowsPerPageOptions={[10, 25, 100]}
				component="div"
				count={rows.length}
				rowsPerPage={rowsPerPage}
				page={page}
				onPageChange={handleChangePage}
				onRowsPerPageChange={handleChangeRowsPerPage}
			/>
		</Paper>
	);
}
