import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Typography from '@mui/material/Typography';

const aboutUsList = [
	{
		first: 'Marți seara',
		second:
			' – pentru copii cu vârste între 7 și 10 ani, cu teme prestabilite care dezvoltă atenția, logica și cooperarea.'
	},
	{
		first: 'Vineri seara',
		second: ' – pentru copii între 11 și 15 ani, cu jocuri de mister, strategie și lucru în echipă.'
	},
	{
		first: 'Sâmbătă',
		second:
			' – eveniment deschis tuturor copiilor între 7 și 15 ani, în care punem la dispoziție întreaga noastră colecție de jocuri de societate. Copiii au libertatea să exploreze, să testeze și să descopere, ghidați cu grijă de echipa noastră.'
	}
];

export default function PageDescription() {
	return (
		<Box maxWidth="md" mx="auto" textAlign="center" marginTop={2}>
			<Typography variant="h4">Despre evenimentele noastre</Typography>
			<Box maxWidth="md" mx="auto" textAlign="left" marginTop={2} gap={2} display="flex" flexDirection={'column'}>
				<Paper sx={{padding: 2}}>
					<Typography>
						Începând cu 24 iunie, în fiecare săptămână organizăm trei evenimente interactive, menite să aducă împreună
						joaca, învățarea și socializarea – într-un mediu sigur, cald și prietenos, ghidate de echipa noastră de
						facilitatori pasionați:
					</Typography>
					{aboutUsList.map(({first, second}) => (
						<Box key={first} flexDirection={'row'} textAlign={'start'} padding={1}>
							<Typography display="inline" fontWeight={700}>
								•
							</Typography>{' '}
							<Typography display="inline" fontWeight={700}>
								{first}
							</Typography>{' '}
							<Typography display="inline">{second}</Typography>
						</Box>
					))}
				</Paper>
				<Paper sx={{padding: 2}}>
					<Typography>
						☕ În tot acest timp, părinții se pot relaxa într-un spațiu separat, cu ceai, cafea și acces la colecția
						noastră de jocuri – pentru că bucuria jocului nu are vârstă!
					</Typography>
				</Paper>
				<Paper sx={{padding: 2}}>
					<Typography>💸 Costul participării la oricare din evenimentele noastre este de 50 de lei.</Typography>
					<Typography>🎉 Prima participare este 25 de lei.</Typography>
				</Paper>
				<Paper sx={{padding: 2}}>
					<Typography fontWeight={700}>📅 Vă doriți să participați, dar programul nu se potrivește?</Typography>
					<Typography>
						Scrieți-ne! Putem organiza evenimente la cerere, în funcție de disponibilitatea voastră și a noastră.
					</Typography>
					<Typography display="inline" fontWeight={700}>
						•
					</Typography>{' '}
					<Typography display="inline" fontWeight={700}>
						Singura condiție:{' '}
					</Typography>
					<Typography display="inline">cel puțin 3 copii participanți.</Typography>
					<Typography>* Pentru evenimentele la cerere nu se aplică reducerea la prima participare</Typography>
				</Paper>
				<Paper sx={{padding: 2}}>
					<Typography fontWeight={700}>
						Pentru înscriere la oricare dintre evenimentele noastre, nu ezitați să ne contactați!
					</Typography>
				</Paper>
			</Box>
		</Box>
	);
}
