import TableCell from '@mui/material/TableCell';
import Link from '@mui/material/Link';

import {Column, Row} from './types';

type Properties = {
	column: Column;
	row: Row;
};
export default function GamesTableCell({column, row}: Properties) {
	const value = row[column.id];
	const getCellValue = () => {
		switch (column.id) {
			case 'image':
				return (
					<img
						src={value as string}
						style={{
							maxWidth: 50,
							maxHeight: 50,
							margin: 'auto'
						}}
					/>
				);
			case 'linkBGG':
				return (
					<Link href={value as string} target="_blank">
						Link
					</Link>
				);
			case 'complexity': {
				const number = Number(value);
				if (number < 1.5) {
					return 'usor';
				} else if (number < 2.5) {
					return 'mediu';
				} else {
					return 'greu';
				}
			}
			default:
				return value;
		}
	};

	return <TableCell align={column.align}>{getCellValue()}</TableCell>;
}
