import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';

import BolderCards from './components/BoldedCard';
import DotCard from './components/DotCard';

const aboutUsList = [
	{
		first: 'O varietate de jocuri atent alese',
		second: 'menite să stimuleze diferite abilități cognitive și sociale'
	},
	{
		first: 'Un mediu distractiv și relaxat',
		second:
			'în care copiii învață să gândească strategic, să își elaboreze și să urmeze un plan, să analizeze situații și să identifice soluții eficiente'
	},
	{
		first: 'Lucru în grupuri mici',
		second:
			'organizate în funcție de vârstă, pentru a susține dezvoltarea socială: copiii învață să relaționeze, să colaboreze, să își gestioneze emoțiile și să își dezvolte reziliența'
	},
	{
		first: 'Sprijin constant din partea echipei',
		second:
			'astfel încât fiecare copil să poată profita la maximum de experiență, într-un mod adaptat ritmului și nevoilor sale'
	}
];
const pentruCine = [
	'Copiii care iubesc jocurile de societate sau vor să le descopere;',
	'Cei care au nevoie de sprijin în dezvoltarea atenției, concentrării sau gândirii logice;',
	'Copiii care vor să își îmbunătățească interacțiunea socială și gestionarea emoțiilor;'
];
const benefits = [
	'Dezvoltarea gândirii critice și a capacității de rezolvare a problemelor;',
	'Îmbunătățirea comunicării și a lucrului în echipă;',
	'Creșterea încrederii în sine și a motivației personale;',
	'Formarea unor strategii sănătoase de gestionare a emoțiilor și a eșecului;',
	'Cultivarea răbdării, perseverenței și a unei atitudini constructive față de provocări.'
];

const desfasurare = [
	{
		first: 'Evenimentele se desfășoară săptămânal',
		second: 'cu o durată de aproximativ 2 ore fiecare'
	},
	{
		first: 'Copiii sunt împărțiți în grupuri mici',
		second: 'în funcție de vârstă și nivel de experiență'
	},
	{
		first: 'Fiecare sesiune include un mix echilibrat de jocuri',
		second: 'în funcție de tema evenimentului'
	},
	{
		first: 'Activitățile sunt ghidate de echipa noastră',
		second: 'cu experiență în lucru cu copiii și pasiune pentru jocuri de societate'
	}
];

export default function AboutUs() {
	return (
		<Box maxWidth="md" mx="auto" textAlign="center" marginTop={2}>
			<Typography variant="h4">Despre noi</Typography>
			<Box maxWidth="md" mx="auto" textAlign="left" marginTop={2}>
				<Paper sx={{padding: 2, flexGrow: 1}}>
					<Typography>
						Suntem o echipă pasionată de jocuri de societate, care a descoperit, de-a lungul timpului, cât de valoroase
						pot fi acestea pentru dezvoltarea personală. Prin intermediul jocului, ne-am antrenat gândirea strategică,
						ne-am format o înțelegere mai clară asupra situațiilor din jur și am învățat să luăm decizii adaptate
						contextului.
					</Typography>
					<Typography>
						Pentru noi, jocul nu este doar o formă de relaxare, ci și un instrument prin care ne menținem mintea activă
						și ne dezvoltăm în mod constant. Din această convingere, ne-am propus să aducem beneficiile jocurilor de
						societate și în universul copiilor. Credem că, într-un cadru distractiv și sigur, aceștia își pot dezvolta
						abilități esențiale precum atenția, gândirea logică, comunicarea și cooperarea.
					</Typography>
				</Paper>
			</Box>
			<Grid container maxWidth="md" mx="auto" spacing={2} my={2}>
				<BolderCards title="Ce oferim?" list={aboutUsList} />
				<DotCard
					title="Pentru cine este potrivit?"
					description="Acest program se adresează copiilor cu vârste între 7 și 15 ani, care sunt curioși, activi și dornici să
							descopere jocuri noi într-un cadru organizat. Este ideal pentru:"
					list={pentruCine}
				/>
				<DotCard
					title="Beneficiile pe termen lung"
					description="Participarea constantă la aceste activități contribuie la:"
					list={benefits}
				/>
				<BolderCards title="Cum funcționează?" list={desfasurare} />
			</Grid>
		</Box>
	);
}
