import Container from '@mui/material/Container';
import Stack from '@mui/material/Stack';
import Typography from '@mui/material/Typography';

import GamesTable from './components/games-table/GamesTable';

export default function GameListPage() {
	return (
		<Container
			sx={{
				display: 'flex',
				flexDirection: 'column',
				alignItems: 'center',
				pt: {xs: 14, sm: 12},
				pb: {xs: 8, sm: 12},
				px: 2
			}}
		>
			<Stack spacing={2} useFlexGap sx={{alignItems: 'center', width: {xs: '100%', sm: '70%'}}}>
				<Typography
					variant="h1"
					sx={{
						fontSize: 'clamp(3rem, 10vw, 3.5rem)',
						textAlign: 'center'
					}}
				>
					Jocuri disponibile
				</Typography>
				<Typography
					variant="h6"
					sx={{
						textAlign: 'center',
						color: 'text.primary',
						width: {sm: '100%', md: '80%'}
					}}
				>
					Mai jos puteți vedea toate jocurile disponibile la noi. Cele cu complexitate ușor sunt disponibile în
					permanență. Dacă doriți jocurile mai complexe, vă rog să ne anunțați și vor fi aduse în locație.
				</Typography>
			</Stack>
			<GamesTable />
		</Container>
	);
}
